.credit-hero-slide {
  position: relative;
  overflow-x: hidden;

  background-color: $primary-active-color;
  background-image: linear-gradient(180.24deg, $dark-gradient-color-1 36.31%, $dark-gradient-color-2 99.79%);

  @include mobile {
    background-image: none;
  }
}

.credit-hero-slide__container {
  @include container;
}

.credit-hero-slide__inner {
  position: relative;
  box-sizing: border-box;
  padding-top: 104px;
  padding-left: rubber(170px);
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: flex-start;
  min-height: 400px;

  background-image: linear-gradient(to right, transparent rubber(52px), $primary-active-color rubber(52px), $primary-active-color rubber(700px), transparent rubber(700px));

  &::before {
    content: "";
    position: absolute;
    left: rubber(52px);
    bottom: 0;
    width: rubber(150px);
    height: 269px;

    background-image: linear-gradient(183.47deg, $decoration-gradient-color-1 66.3%, $decoration-gradient-color-2 97.14%);
  }

  &::after {
    content: "";
    position: absolute;
    left: rubber(700px);
    bottom: 0;
    width: 270px;
    height: 269px;

    background-image: linear-gradient(183.47deg, $decoration-gradient-color-1 27.26%, $decoration-gradient-color-2 97.14%);
  }

  @include tablet {
    padding-top: 67px;
    padding-left: 41px;
    min-height: 300px;

    background-image: linear-gradient(to right, $primary-active-color 411px, transparent 411px);

    &::before {
      left: -$min-viewport--desktop;
      bottom: 0;
      width: $min-viewport--desktop;
      height: 100%;

      background: $primary-active-color;
    }

    &::after {
      left: 411px;
      width: 179px;
    }
  }

  @include mobile {
    padding-top: 42px;
    padding-left: 0;
    min-height: 226px;

    background: none;

    &::before,
    &::after {
      content: initial;
    }
  }
}

.credit-hero-slide img {
  position: absolute;
  z-index: 1;
  top: 60px;
  left: calc(50% + 15px);
  width: 444px;
  height: auto;

  @include tablet {
    top: 42px;
    left: calc(50% - 31px);
    width: 346px;
  }

  @include mobile {
    top: 31px;
    left: calc(50% + 56px);
    width: 255px;
  }
}

.credit-hero-slide h2 {
  margin-top: 0;
  margin-bottom: 15px;

  font-weight: 700;
  font-size: 54px;

  color: $alternative-color;

  @include tablet {
    font-size: 46px;
    margin-bottom: 12px;
  }

  @include mobile {
    font-size: 38px;
    margin-bottom: 12px;
  }
}

.credit-hero-slide p {
  margin-top: 0;
  margin-bottom: 37px;

  font-size: 22px;

  color: $alternative-legend-color;

  @include tablet {
    margin-bottom: 34px;

    font-size: 18px;
  }

  @include mobile {
    margin-bottom: 28px;

    font-size: 16px;
  }
}

.credit-hero-slide a {
  padding: 17px 36px 18px;

  font-weight: 500;
  font-size: 18px;

  color: $primary-color;
  background-color: $alternative-color;
  border-radius: 4px;
  text-decoration: none;

  &:hover {
    color: $alternative-color;
    background-color: $primary-color;
  }

  &:active {
    opacity: 0.7;
  }

  @include tablet {
    padding: 16px 31px 16px;

    font-size: 16px;
  }

  @include mobile {
    padding: 14px 31px 15px;

    font-size: 14px;
  }
}
